@font-face {
  font-family: 'Dolce Vita Heavy A Few Extra Pounds';
  font-style: normal;
  font-weight: normal;
  src: local('Dolce Vita Heavy A Few Extra Pounds'), url('dolce vita heavy bold.woff') format('woff');
  }

.App {
  text-align: center;
}

.App .navbar-brand {
  font-weight: bold;
}
.App-header {
  background-color: #282c34;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-header h1{
  font-family:'Dolce Vita Heavy A Few Extra Pounds';
    font-weight:bold;
}
.row
{
  width: 100%;
  margin-left: 0;
}
.button{
  color: White;
  height: 50px;
  width: 33.3333%;
  background-color: #555555;
  border: none;
  transition: 0.5s;
  font-size:15px;
  font-weight: bold;
  float: left;
}
.half{
  min-width: 50%;
}
.button:hover,.active{
  background-color: white;
  color: #282c34;
  cursor: pointer;

}

.container1{
  width: 500px;
  height: 1000px;
  /* box-shadow: 10px 10px 5px #333333; */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: cadetblue;
  display: flex;
  flex-direction: column;
  align-items: left;
  /* justify-content: center; */
}
#containmean{
  text-align: left;
  font-size: calc(8px + 2vmin); 
  background-color: #282c34;
  transition: 0.5s;
}
#containmean p{
  margin-left: 50px;
}
#containmean h5{
  margin-left: 30px;
}
#addWords h4{
  color: #282c34;
}
#addWords form{
  text-align: left;
  margin-left: 40px;
}
#addWords form label{
  width: 100px;
}

#addWords form div{
  margin-top:8px;
}

#addWords input[type = text]{
  width: 80%;
  padding: 12px 20px;
  margin: 8px 0;
  background-color: #282c34;
  color: white;
  font-weight: bold;
  border: none;
}
#addWords textarea{
  max-width: 400px;
  max-height: 200px;
  width: 80%;
  background-color:#282c34;
  color: white; 
  font-family:'Dolce Vita Heavy A Few Extra Pounds';
  
}

@media screen and (max-width: 600px)
{
  .container{
    width: 100%;
    height: 700px;
  }

  #addWords textarea{
    width: 80vw;
  }
}